@import 'variables';

.btn-banner {
  font-size: 1.5rem;
  color: $blue;
  font-weight: bold;
  border-radius: 0.25em;
  border: 1px solid $yellowDark;
  background-image: linear-gradient(to right, $yellowDark, $yellowLight);
  padding: 0.75em 3em;
  box-shadow: inset 0px 1px 0px 0px #ffffff;
}
